'use client';

import { useState, useRef, useEffect } from 'react';
import ChatbotHeader from '@/components/chatbot/ChatbotHeader';
import ChatMessage from '@/components/chatbot/ChatMessage';
import ChatInput from '@/components/chatbot/ChatInput';
import ThemeToggle from '@/components/chatbot/ThemeToggle';

type Message = {
  id: string;
  content: string;
  role: 'user' | 'assistant';
  timestamp: Date;
};

type Theme = 'dark' | 'gradient';

const initialMessages: Message[] = [
  {
    id: '1',
    content: 'Hello! I\'m your AI assistant. How can I help you today?',
    role: 'assistant',
    timestamp: new Date(Date.now() - 3600000),
  },
  {
    id: '2',
    content: 'Can you help me with a coding problem?',
    role: 'user',
    timestamp: new Date(Date.now() - 1800000),
  },
  {
    id: '3',
    content: 'Sure! I\'d be happy to help with coding problems. What language are you working with and what challenge are you facing?',
    role: 'assistant',
    timestamp: new Date(),
  },
];

export default function ChatbotPage() {
  const [messages, setMessages] = useState<Message[]>(initialMessages);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [copiedMessageId, setCopiedMessageId] = useState<string | null>(null);
  const [currentTheme, setCurrentTheme] = useState<Theme>('gradient');
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSendMessage = async () => {
    if (!input.trim() || isLoading) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      content: input,
      role: 'user',
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 800));

      let response = '';
      if (currentTheme === 'dark') {
        response = `ðŸŒ™ **Dark Mode**: I received: "${input}"\n\nIn dark mode, the interface is sleek and easy on the eyes. How else can I assist you in this cool theme?`;
      } else {
        response = `ðŸŒˆ **Gradient Mode**: I received: "${input}"\n\nThe vibrant gradient theme makes our conversation more colorful! What would you like to explore next?`;
      }

      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: response,
        role: 'assistant',
        timestamp: new Date(),
      };

      setMessages(prev => [...prev, assistantMessage]);
    } catch (error) {
      console.error('Error sending message:', error);
      
      const errorMessage: Message = {
        id: (Date.now() + 2).toString(),
        content: 'Sorry, I encountered an error. Please try again.',
        role: 'assistant',
        timestamp: new Date(),
      };
      
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleClearChat = () => {
    setMessages([
      {
        id: '1',
        content: `Chat cleared! I'm ready to help you in ${currentTheme} mode.`,
        role: 'assistant',
        timestamp: new Date(),
      },
    ]);
  };

  const handleCopyMessage = (content: string, messageId: string) => {
    navigator.clipboard.writeText(content);
    setCopiedMessageId(messageId);
    setTimeout(() => setCopiedMessageId(null), 2000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const toggleTheme = () => {
    setCurrentTheme(currentTheme === 'dark' ? 'gradient' : 'dark');
  };

  return (
    <div className={`flex flex-col h-screen transition-all duration-300 ${
      currentTheme === 'dark' 
        ? 'bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900' 
        : 'bg-gradient-to-br from-indigo-50 via-purple-50 to-pink-50'
    }`}>
      <ChatbotHeader 
        onClearChat={handleClearChat}
      />
      
      {/* Theme Toggle Button */}
      <div className="flex justify-center py-2">
        <ThemeToggle 
          currentTheme={currentTheme}
          onToggle={toggleTheme}
        />
      </div>
      
      <div className="flex-1 overflow-hidden flex flex-col max-w-4xl mx-auto w-full p-4 md:p-6">
        {/* Messages container */}
        <div className="flex-1 overflow-y-auto mb-4 space-y-6 p-2 md:p-4">
          {messages.map((message) => (
            <ChatMessage
              key={message.id}
              message={message}
              isCopied={copiedMessageId === message.id}
              onCopy={() => handleCopyMessage(message.content, message.id)}
              theme={currentTheme}
            />
          ))}
          
          {isLoading && (
            <div className="flex items-center space-x-3">
              <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                currentTheme === 'dark' 
                  ? 'bg-gray-800' 
                  : 'bg-gradient-to-r from-indigo-100 to-purple-100'
              }`}>
                <div className={`w-6 h-6 rounded-full animate-spin border-3 ${
                  currentTheme === 'dark'
                    ? 'border-gray-700 border-t-indigo-400'
                    : 'border-indigo-200 border-t-indigo-500'
                }`} />
              </div>
              <span className={`text-sm ${
                currentTheme === 'dark' ? 'text-gray-400' : 'text-indigo-600'
              }`}>
                Thinking...
              </span>
            </div>
          )}
          
          <div ref={messagesEndRef} />
        </div>

        {/* Input area */}
        <ChatInput
          input={input}
          setInput={setInput}
          isLoading={isLoading}
          onSend={handleSendMessage}
          onKeyPress={handleKeyPress}
          theme={currentTheme}
        />
      </div>
      
      <footer className="text-center py-4">
        <p className={`text-sm ${
          currentTheme === 'dark' ? 'text-gray-500' : 'text-purple-600'
        }`}>
          Current mode: <span className="font-medium capitalize">{currentTheme}</span>
        </p>
      </footer>
    </div>
  );
}